import * as Yup from 'yup';

export const formSchema = Yup.object().shape({
    name: Yup.string()
      .required('  ')
      .min(3, ' 3 ')
      .matches(/^[a-zA-Z\s]+$/, '   '),
    number: Yup.string()
      .required('  ')
      .length(9, '  9    456-58-78')
      .matches(/^[0-9]{3}-[0-9]{2}-[0-9]{2}$/, ' '),
  });
  